#page-numbers {
    list-style: none;
    display: flex;
  }
  
  #page-numbers > li {
    margin-right: 0.3em;
    color: blue;
    user-select: none;
    cursor: pointer;
  }
  table{
    font: bold 25px;
  }
  #container{
    margin-top: 4em;
    max-height: 100vh;
    height: 100vh;
    width: 90%;
  }
  
  th{
    font-weight: 600  !important;
    font-size: larger !important
  }
  td{
    font-weight: normal  !important;
    font-size: large !important;
  }
  .modal img{
    width: 80%;
    height: 80%;
  }
.footermain{
    width: 100vw;
    height: auto;
   
    background:  rgba(0,212,255,0.9) ;
    color: #000 !important;
    font-weight: 700;
    

}

.footermain h6{
    font-weight:bolder;
    color: white;
    font-size: 26px;
}
.footermain hr{
    width: 120px;
    height: 2px;
    padding-left: auto;
    padding-right: auto;
    
}
.footermain a{
    color: #000 !important;
    
}
.footermain a:hover{
    color: blue !important;
    text-decoration: underline;
    
}
.footermain a:active{
    color: #A63093 !important;
    
}
.footermain i{
    color: #A63093 !important;
    text-align: left;
    
}
.footer-copyright{
    color: #000 !important;
    font-weight: bold;
    
}
.footermain li{
    font-weight: normal;
    list-style-type: none;
    text-align: start;
}


