/*reservation page*/
#reservation{
    background-image: url('../images/triangles.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-color: rgba(0,0,0,0.5);
}
#reservation form{
    background-color: white;
    border-radius: 3px;
    padding: 3px;
    font-size: 17px;
}




