/* new review*/

#new-review-form{
    position: relative;
    top: 7vh;
}
#new-review h2{
    position: relative;
    top: 5vh;
}

/*feedback button*/

#feedback-btn{
    transform: rotate(-0.25turn);
    position: fixed;
    right: -35px;
    top: 200px;
    z-index: 2;
    height: 50px;
    padding-top: 12px;
    padding-bottom: 12px;
}

/*about us section*/
#about-img{
    height: 400px;
    width: 1000px;
}

/* apartments display filter*/
.filter{
    height: 50px;
    /*width: ;*/
    border: 1px solid;
    border-radius: 2px;
    background-color: #A63093;
    color: white;
}
.filter option{
    font-weight: 200;
    font-size: large;
}

/* signup */
#signup-part-one{
    position: relative;
    top:55px;
   
}

@media (min-width:767px) {
    #new-review-form{
        top: 17vh;
    }
  
}
@media(max-width:500px){
    .agent-img{
        width: auto;
        max-width: 300px;
        height: auto;
        max-height: 260px;
    }
}
@media (max-width:600px) {
    
    .house-details-img{
        max-height: 9rem;/*formerlly 12*/
        width: 100%;
    }
    #img-holder{
        min-height: 80px;
    }
    
  }

  @media (max-width:989px) {
    
    .house-details-img{
        max-height: 29rem;
        width: 100%;
    }
    
  }
  @media (min-width:900px) {
    #address{
        height: auto;
        overflow: scroll !important;
        
    }
   
}
@media (max-width:1279px) {
    .house-details-img{
        width: auto;
        max-width: 100%;
        height: 100%;
    }
}
 