 li{
  font-size: medium !important;
 }
 p{
  font-size: medium !important;
 }
 h3{
  color:purple !important;
 }
 .main-h{
  text-transform:uppercase
 }
 #apppage .view {
   
   background-repeat: no-repeat;
   background-size: cover;
   background-position: center ;
  
   min-height: 100vh;
 
  /*animation: slidingo 28s linear infinite 0s;
  animation-timing-function:ease-in-out;
   backface-visibility: hidden;*/
  background-image: url('../images/home-lg.jpg');
 }

 #kribi p,#douala p,#limbe p, #yaoundé p,#buea p{
  font-size: 30px !important;
  position: relative;
  top: 50px;
 }

 @keyframes slidingo {
  0%{
    background-image: url('../images/home1.jpg');
  }
 25%{
    background-image: url('../images/large-home.png');
  }
  50%{
    background-image: url('../images/home1.jpg');
  }
  75%{
    background-image: url('../images/large-home.png');
  }
  100%{
    background-image: url('../images/home1.jpg');
  }
  
 }
 #apppage h6 {
   line-height: 1.7;
 }
 #kribi,#douala,#yaoundé,#limbe,#buea{
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center ;
  height:18vh;
 width: 75vw;
 }
 #kribi{
  background-image: url('../images/kribi.jpg');
 }
 #douala{
  background-image: url('../images/ecologie-culturelle-douala.jpg')
 }
 #yaoundé{
  background-image: url('../images/monument-de-la-reunification.jpg')
 }
 #limbe{
  background-image: url('../images/hotel-beach.jpg')
 }
 #buea{
  background-image: url('../images/monument.jpg')
 }
 #kribi .city,#douala .city,#limbe .city,#yaoundé .city,#buea .city{
  width: 100% !important;
  height: 100%;
  background-color: rgba(0,0,0,0.1) !important;
 }
 #kribi .city:hover,#douala .city:hover,#limbe .city:hover,#buea .city:hover,#yaoundé .city:hover{
  background-color: rgba(0,0,0,0.9) !important;
  transition: background-color 1s;
  
 }
 #cities-mobile{
  position: relative;
  top: 38px;
 }
 .subh p{
  font-size: 23px !important;
  padding-right: 4px;
 }
 .slash{
  border-right: 3px solid white;
  height: 40px;
 }
/*#touristic{
  position: relative;
  top: 8vh;
 }*/
 #touristic:active{
  transition: display 2s;
 }

 @media (max-width:500px) {
    #apppage .view{
      /*background-size: 100vw 100%;*/
      background-image: url('../images/home-mb.jpg');
      overflow:auto;
    
    
    }
    
    #touristic{
      top: 5vh;
    }
    .view .container{
      padding-top: 2em;
    }
    #feedback-btn{
      right:0px !important;
      z-index: 1;
      display: none;
    }
    #homepage-body {
      padding-top: 9rem;
      
    }
    .subh p{
      font-size: 20px !important;
      padding-right: 2px;
     }
     .slash{
      border-right: 3px solid white;
      height: 68px;
     }
  
  
 }
 @media (min-width:575px){
  #kribi,#douala,#yaoundé,#limbe,#buea{
    height:150px;
    width: 200px;
  }
  
 }
 @media (min-width:756px){
  #homepage-body{
    padding-top: 18rem;
  }
  #homepage-body h1{
    font-size: 50px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
 }
 @media(max-width:1200px){
  #homepage-body{
    padding-top: 8rem;
  }
 }


