#page-numbers {
    list-style: none;
    display: flex;
  }
  
  #page-numbers > li {
    margin-right: 0.3em;
    color: blue;
    user-select: none;
    cursor: pointer;
  }
  table{
    font: bold 25px;
  }
  #container{
    margin-top: 4em;
    max-height: 100vh;
    height: 100vh;
    width: 90%;
  }
  th{
    font-weight: 600  !important;
    font-size: larger !important
  }
  td{
    font-weight: normal  !important;
    font-size: large !important;
  }
  .modal img{
    width: 80%;
    height: 80%;
  }