#authPage{
    
    background-image: url('../../images/hse.jpg');
    width: 100%;
    max-width: 100vw;
}
.inl{
    font-size: larger;
    font-weight: bold;
}
.auth{
    background: linear-gradient(45deg,
    rgba(42, 27, 160, 0.7),
    #A43393 100%
    );
    width: 100%;
}

label{
    color: black !important;
    font-weight: lighter !important;
}
.glyph{
    position: relative;
    top: 2em;
    right: 5em;
}

/* upload house*/

#upload{
    position: relative;
    top:28px;
}
#upload-ownership{
    position: relative;
    top: 10vh;
}

.show-password{
    position: relative;
    top: -19px;
    left: 20px;
}
@media   (max-width:768px) {
    .glyph{
        position: relative;
        top: -3.4em;
        right: -11.5em;
    }
}
@media (max-width:500px) {
    .glyph{
        position: relative;
        top: -3.4em;
        right: 9em;
    }
}
@media (max-width:600px) {
    .glyph{
        position: relative;
        top: -3.4em;
        right: -20em;
    }
}
@media (min-width:769px) {
    .glyph{
        position: relative;
        top: -3.4em;
        right: -25em;
    }
    .show-password{
        left: 25px;
    }
}